import { _decorator, Component, EventTouch, math, misc, Node, UITransform, Vec3 } from 'cc';
import { Plane } from './Plane';
const { ccclass, property } = _decorator;

@ccclass('JoyStick')
export class JoyStick extends Component {
    static instance: JoyStick;
    @property(Node)
    public Stick: Node = null;
    @property
    public maxR: number = 100;
    public Pos;
    public VecY = new Vec3(0, 0, 0);
    public angleDeg;
    @property(Node)
    public plane: Node = null;
    public moveSpeed: number = 200;
    
    start() {

    }

    update(dt: number) {
        
    }
    protected onLoad(): void {
        JoyStick.instance = this;
        
        // this.Stick.setPosition(new Vec3(0, 0));
        this.node.on(Node.EventType.TOUCH_MOVE, this.onStickMove, this);
        this.node.on(Node.EventType.TOUCH_END, this.onStickEnd, this);
        this.node.on(Node.EventType.TOUCH_CANCEL, this.onStickEnd, this);

    }
    onStickMove(event: EventTouch, dt: number){
        let wp = event.getUILocation();
        this.Pos = this.node.getComponent(UITransform).convertToNodeSpaceAR(new Vec3(wp.x, wp.y));
        let stickMag = this.Pos.length();
        if(stickMag <= 0){
            return;
        }
        if(stickMag > this.maxR){
            this.Pos.x = this.Pos.x * this.maxR/stickMag;
            this.Pos.y = this.Pos.y * this.maxR/stickMag;
        }
        this.Stick.setPosition(this.Pos);

        let angle = this.Stick.angle; // Góc quay của joystick
        let distance = math.Vec3.distance(this.VecY, this.Pos); // Khoảng cách từ tâm của joystick đến điểm đang chạm
        
        // Tính toán vector di chuyển dựa trên góc và khoảng cách
        let radian = misc.degreesToRadians(angle); // Chuyển đổi góc sang radian
        let deltaX = Math.cos(radian) * distance; // Tính toán delta X
        let deltaY = Math.sin(radian) * distance; // Tính toán delta Y
        
        // Di chuyển đối tượng
        let posX = this.plane.position.x;
        let posY = this.plane.position.y;

        posX += deltaX * this.moveSpeed * dt;
        posY += deltaY * this.moveSpeed * dt;
        let posPlane = new Vec3(posX, posY);
        this.plane.setPosition(posPlane);
    }
    onStickEnd(event: EventTouch){
        // this.Stick.setPosition(new Vec3(0, 0));
    }
    countAngleStick(event, dt: number){
        
    }
    
}


